<script setup>
import Dice from '@/components/Layout/AssitTool/Dice.vue'
import { ref } from 'vue'
const dices = ref([{ num: 1, point: 6 }])
const result = ref(0)
const addDice = () => {
  dices.value.push({ num: 1, point: 6 })
}
const removeDice = () => {
  if (dices.value.length === 1) {
    return
  }
  dices.value.pop()
}
const roll = () => {
  let tmp = 0
  dices.value.forEach((e) => {
    if (e.num >= 1 && e.point >= 1) {
      for (let i = 0; i < e.num; i++) {
        tmp += Math.floor(Math.random() * e.point) + 1
      }
    }
  })
  result.value = tmp
}
</script>

<template>
  <div class="container">
    <el-container>
      <el-aside width="60%">
        <el-scrollbar height="600px">
          <div v-for="(dice, index) in dices" :key="index">
            <Dice
              v-model:dice="dice.num"
              v-model:point="dice.point"
              v-model:addDice="addDice"
              v-model:removeDice="removeDice"
            ></Dice>
          </div>
        </el-scrollbar>
      </el-aside>
      <el-main>
        <div class="getResult">
          <div class="roll" @click="roll">ROLL!</div>
          <div class="result">{{ result }}</div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>
<style scoped>
.container {
  width: 85%;
}
.el-aside {
  padding-top: 20px;
  border-right: 3px solid #e9f2fc;
}
.getResult {
  display: flex;
  align-items: center;
}
.roll {
  width: 60px;
  height: 60px;
  background-color: red;
  line-height: 60px;
  text-align: center;
  border-radius: 30px;
  font-weight: bold;
  color: white;
  border: 1px solid black;
  cursor: pointer;
}
.result {
  margin-left: 100px;
  font-size: 60px;
}
</style>
